import React, { Component  } from 'react';
import { CardList } from './component/card-list/card-list.component.jsx';
import { SearchBox } from './component/search-box/search-box.component.jsx';
import Navbar from 'react-bootstrap/Navbar';
import logo from './logo.svg'
import './App.css';


class App extends Component {

constructor () {
  super();

this.state = {
  countries : [],
  searchField : '',
  show: false
}
}

handleModal(){
  this.setState({show:true})
}

componentDidMount(){
  fetch('https://cdn.jsdelivr.net/npm/country-flag-emoji-json@2.0.0/dist/index.json')
  .then(response => response.json())
  .then(users => this.setState({countries : users}))
}

  render(){

      const { countries , searchField } = this.state;

      const FilteredCountries = countries.filter(monster => monster.name.toLowerCase().includes(searchField.toLowerCase()))
 
      return (
        <div className="App">
          <Navbar bg="dark">
          <Navbar.Brand href="#home">
                    <img
                      src={logo}
                      width="60"
                      height="60"
                      className="d-inline-block align-top"
                      alt="React Bootstrap logo"
                    />
              </Navbar.Brand>
         <SearchBox 
         placeholder='search country'
         handlechange={e => this.setState({searchField : e.target.value})} />
            </Navbar>
            <div className="text-start text-count text-blue">showing {FilteredCountries.length} countries</div>
          <CardList countries={FilteredCountries} />
        </div>
      );
  }
}

export default App;
